/*bold version*/
@font-face {
  font-family: 'Niveau Grotesk';
  src: url('../../fonts/Niveau Grotesk Medium.otf'); /* IE9 Compat Modes */
  src: local('Niveau Grotesk'), local('Niveau Grotesk'),
    /* Duplicated name with hyphen */ url('../../fonts/Niveau Grotesk Medium.otf')
      format('truetype');
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: 'Niveau Grotesk';
  src: url('../../fonts/Niveau Grotesk Regular.otf');
  src: local('Niveau Grotesk'), local('Niveau Grotesk'),
    url('../../fonts/Niveau Grotesk Regular.otf') format('truetype');
  font-weight: 600;
  font-display: swap;
}
@font-face {
  font-family: 'Niveau Grotesk';
  src: url('../../fonts/Niveau Grotesk Light.otf'); /* IE9 Compat Modes */
  src: local('Niveau Grotesk'), local('Niveau Grotesk'),
    /* Duplicated name with hyphen */ url('../../fonts/Niveau Grotesk Light.otf')
      format('truetype');
  font-display: swap;
}
@font-face {
  font-family: 'Timberline Regular';
  src: url('../../fonts/Timberline Regular.woff'); /* IE9 Compat Modes */
  src: local('Timberline Regular'), local('Timberline Regular'),
    /* Duplicated name with hyphen */ url('../../fonts/Timberline Regular.woff')
      format('truetype');
  font-display: swap;
}

body {
  color: #45443f;
  letter-spacing: 0.04em;
}
* {
  margin: 0;
  padding: 0;
  text-rendering: auto;
  font-family: 'Niveau Grotesk';
  box-sizing: border-box;
}

A {
  @include prefix(transition, (all 170ms));
  text-decoration: none;
  color: inherit;
}

IMG[src=''] {
  visibility: hidden;
  width: 0;
  height: 0;
}

IMG,
TABLE,
FIELDSET {
  border: 0;
}

OL,
UL {
  list-style-type: none;
}

LEGEND {
  display: none;
}

BUTTON,
INPUT {
  border-radius: 0;
  outline: 0 none;

  &[type='search']::-webkit-search-decoration,
  &[type='search']::-webkit-search-cancel-button,
  &[type='search']::-webkit-search-results-button,
  &[type='search']::-webkit-search-results-decoration {
    display: none;
  }

  &[type='number'] {
    -moz-appearance: textfield;
  }
  &[type='number']::-webkit-inner-spin-button,
  &[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
    appearance: none;
    margin: 0;
  }
}

BUTTON {
  @include prefix(appearance, none);
  background: {
    color: transparent;
    image: none;
  }
  overflow: visible;
  cursor: pointer;
  border: 0 none;

  &:focus {
    outline: 0 none;
  }
}

TEXTAREA {
  resize: none;
}

HR {
  color: black;
  background-color: black;
  border: none;
}

MARK {
  color: inherit;
  background-color: transparent;
}

.primary-select {
  border-radius: 0 !important;
  &:focus-within,
  &:focus,
  &:hover {
    border-color: #2a676b !important;
    box-shadow: none !important;
  }
  .react-dropdown-select-dropdown {
    border: none !important;
  }
  .react-dropdown-select-item:hover,
  .react-dropdown-select-item-selected {
    background: #2a676b !important;
    color: white;
  }
}

.blk-filter-results {
  @include desktop {
    .row {
      .col-lg-3 {
        width: 22% !important;
      }
      .col-lg-9 {
        width: 78% !important;
      }
    }
  }
}

.fb_iframe_widget {
  iframe {
    z-index: 98 !important;
  }
}
